:root {
  /** Typography */

  /* Theme typefaces */

  /* Header */
  --font-family-header:    'Libre Baskerville', 'Times New Roman', serif;
  --line-height-header:    1.5;
  --letter-spacing-header: normal;
  --font-weight-header:    700;

  /* Subheader */
  --font-family-subheader:    'Libre Baskervile', 'Times New Roman', serif;
  --line-height-subheader:    1.5;
  --letter-spacing-subheader: normal;
  --font-weight-subheader:    700;

  /* Body */
  --font-family-body:    'Source Sans Pro', Helvetica, Arial, sans-serif;
  --line-height-body:    1.8;
  --letter-spacing-body: normal;
  --font-weight-body:    400;

  /* Small */
  --font-family-small:    'Source Sans Pro', Helvetica, Arial, sans-serif;
  --line-height-small:    1.4;
  --letter-spacing-small: 1px;
  --font-weight-small:    400;


  /* Font Levers */

  --font-base:  18px;
  --font-ratio: 1.125;

  /* Modular scale */
  --font-size-1:  calc(var(--font-size-2) / var(--font-ratio));
  --font-size-2:  calc(var(--font-size-3) / var(--font-ratio));
  --font-size-3:  calc(var(--font-size-4) / var(--font-ratio));
  --font-size-4:  calc(var(--font-size-5) / var(--font-ratio));
  --font-size-5:  calc(var(--font-size-6) / var(--font-ratio));
  --font-size-6:  var(--font-base);
  --font-size-7:  calc(var(--font-size-6) * var(--font-ratio));
  --font-size-8:  calc(var(--font-size-7) * var(--font-ratio));
  --font-size-9:  calc(var(--font-size-8) * var(--font-ratio));
  --font-size-10: calc(var(--font-size-9) * var(--font-ratio));
  --font-size-11: calc(var(--font-size-10) * var(--font-ratio));
  --font-size-12: calc(var(--font-size-11) * var(--font-ratio));
  --font-size-13: calc(var(--font-size-12) * var(--font-ratio));
  --font-size-14: calc(var(--font-size-13) * var(--font-ratio));
  --font-size-15: calc(var(--font-size-14) * var(--font-ratio));
  --font-size-16: calc(var(--font-size-15) * var(--font-ratio));


  /* Token size */
  --font-size-small-1:      var(--font-size-1);
  --font-size-small-2:      var(--font-size-2);
  --font-size-small-3:      var(--font-size-3);
  --font-size-small-4:      var(--font-size-4);
  --font-size-body-1:       var(--font-size-5);
  --font-size-body-2:       var(--font-size-6);
  --font-size-header-1:     var(--font-size-7);
  --font-size-header-2:     var(--font-size-8);
  --font-size-header-3:     var(--font-size-9);
  --font-size-header-4:     var(--font-size-10);
  --font-size-header-5:     var(--font-size-11);
  --font-size-header-6:     var(--font-size-12);
  --font-size-header-7:     var(--font-size-13);
  --font-size-header-8:     var(--font-size-14);
  --font-size-header-9:     var(--font-size-15);
  --font-size-header-10:    var(--font-size-16);
  --font-size-subheader-1:  var(--font-size-7);
  --font-size-subheader-2:  var(--font-size-8);
  --font-size-subheader-3:  var(--font-size-9);
  --font-size-subheader-4:  var(--font-size-10);
  --font-size-subheader-5:  var(--font-size-11);
  --font-size-subheader-6:  var(--font-size-12);
  --font-size-subheader-7:  var(--font-size-13);
  --font-size-subheader-8:  var(--font-size-14);
  --font-size-subheader-9:  var(--font-size-15);
  --font-size-subheader-10: var(--font-size-16);


  --line-height-small-1:      1.4;
  --line-height-small-2:      1.4;
  --line-height-small-3:      1.4;
  --line-height-small-4:      1.4;
  --line-height-body-1:       1.8;
  --line-height-body-2:       1.8;
  --line-height-header-1:     1.50;
  --line-height-header-2:     1.45;
  --line-height-header-3:     1.40;
  --line-height-header-4:     1.35;
  --line-height-header-5:     1.30;
  --line-height-header-6:     1.25;
  --line-height-header-7:     1.20;
  --line-height-header-8:     1.15;
  --line-height-header-9:     1.10;
  --line-height-header-10:    1;
  --line-height-subheader-1:  1.50;
  --line-height-subheader-2:  1.45;
  --line-height-subheader-3:  1.40;
  --line-height-subheader-4:  1.35;
  --line-height-subheader-5:  1.30;
  --line-height-subheader-6:  1.25;
  --line-height-subheader-7:  1.20;
  --line-height-subheader-8:  1.15;
  --line-height-subheader-9:  1.10;
  --line-height-subheader-10: 1;

  /* Spacing predefined values, do not edit */
  --spacing-base-compact:      4px;
  --spacing-increment-compact: 4px;
  --spacing-base-default:      8px;
  --spacing-increment-default: 8px;
  --spacing-base-loose:        16px;
  --spacing-increment-loose:   10px;

  /* Spacing Levers, set these using the predefined values above */
  --spacing-base:      var(--spacing-base-default);
  --spacing-increment: var(--spacing-increment-default);

  /* Spacing Scale, use these tokens in your styles */
  --space-1:  calc(var(--spacing-base) / 2);
  --space-2:  var(--spacing-base);
  --space-3:  calc(var(--space-2) + var(--spacing-increment));
  --space-4:  calc(var(--space-3) + var(--spacing-increment));
  --space-5:  calc(var(--space-4) + var(--spacing-increment));
  --space-6:  calc(var(--space-5) + var(--spacing-increment));
  --space-7:  calc(var(--space-6) + var(--spacing-increment));
  --space-8:  calc(var(--space-7) + var(--spacing-increment));
  --space-9:  calc(var(--space-8) + var(--spacing-increment));
  --space-10: calc(var(--space-9) + var(--spacing-increment));
  --space-11: calc(var(--space-10) + var(--spacing-increment));

  /* Layout */
  --max-width:      1024px;
  --max-width-xl:   1130px;
  --heading-height: 72px;

  // Grid
  --col-width: 48.5px;
  --gutter:    18px;

  // Article
  --article-content-width: 634px;

  /* Brand Color Pallete */
  --c-black:               #000000;
  --c-dark-gray:           #333333;
  --c-gray:                #afafaf;
  --c-light-gray:          #f3f3f3;
  --c-white:               #ffffff;
  --c-brick-red:           #9c1e14;
  --c-WNYC-fireengine-red: #de1e3d;
  --c-reddish-orange:      #de5e36;
  --c-banana-yellow:       #ffc400;
  --c-taxicab-yellow:      #e3d54a;
  --c-dusk-blue:           #244c84;
  --c-brown-grey:          #939393;
  --c-dirty-salmon:        #f0eeea;
  --c-purple:              #060507;

  /* Themable Tokens, Light Colors (Default) */
  --c-background:           var(--c-white);
  --c-background-muted:     var(--c-dirty-salmon);
  --c-background-highlight: var(--c-taxicab-yellow);
  --c-text:                 var(--c-dark-gray);
  --c-text-muted:           var(--c-gray);
  --c-text-error:           var(--c-brick-red);
  --c-text-hover:           var(--c-reddish-orange);
  --c-accent:               var(--c-banana-yellow);
  --c-accent2:              var(--c-dusk-blue);
  --c-button-donate:        var(--c-reddish-orange);
  --c-button-primary:       var(--c-banana-yellow);
  --c-button-text:          var(--c-white);

  /* Themable Tokens, Borders */
  --b-border-subtle: 2px dashed var(--c-dirty-salmon);
  --b-border-normal: 3px solid var(--c-black);
  --b-border-bold:   5px dashed var(--c-reddish-orange);

}
